/** @jsxImportSource @emotion/react */
import React, { useMemo } from 'react'
import MuiButton from '@mui/material/Button'
// import log from 'loglevel'

import { DataGrid, renderers, DataGridRecordCount } from '../DataGrid'
import { Box } from '../../types'
import { customerColumnDefs } from './columnDefs'
import { useNavigate } from '../AppRouter'

export interface CustomerBoxesListingProps {
  boxes: Box[]
}

export interface BoxRow extends Box {
  forms: {
    label: string
    onClick: () => void
  }
}

export const CustomerBoxesListing = ({ boxes }: CustomerBoxesListingProps): JSX.Element => {
  const navigate = useNavigate()

  // STATE AND REFS

  const rows = useMemo<BoxRow[]>(() => {
    return boxes.map((box) => {
      return {
        ...box,
        forms: {
          label: box.submissionCount ? 'Chain of Custody' : '',
          onClick: () => {
            navigate.chainOfCustodyForm({ boxId: box.id })
          },
        },
      }
    })
  }, [boxes])

  // RENDER

  return (
    <>
      <div css={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        <div css={{ minHeight: '2em', gap: '0.5em', display: 'flex' }}>
          <MuiButton
            variant="outlined"
            onClick={() => {
              navigate.boxSubmission()
            }}
          >
            Create New Submission
          </MuiButton>
          <DataGridRecordCount
            total={boxes.length}
            visible={rows.length}
            descriptor="submissions"
            css={{ marginLeft: 'auto' }}
          />
        </div>
        <div css={{ flex: 1 }}>
          <DataGrid<BoxRow> rows={rows} columnDefs={customerColumnDefs} frameworkComponents={renderers} />
        </div>
      </div>
    </>
  )
}
